<template>
    <div >
        <router-view/>
    </div>
</template>
<script>
    import AUTH from '@/config/web/auth'
    export default {
        data() {
            return {}
        },
        created() {
            const member = AUTH.getSession();
            if (!member) {
                this.$router.push('/');
                return;
            }
        },

    }
</script>

<style lang="scss" scoped>
    .page-container {
        width: 100%;
    }
</style>